/// <reference types="@angular/localize" />

import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormsModule,
  ReactiveFormsModule,
} from '@angular/forms';
import { AuthService } from '../../services/auth.service';
import { LocalstorageService } from '../../services/localstorage.service';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
// import { InputTextModule } from 'primeng/inputtext';
// import { Button } from 'primeng/button';
import { AuthGuard } from '../../services/auth-guard.service';
// import { ErrorStateMatcher } from '@angular/material/core';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatButtonModule } from '@angular/material/button';
import { UsersService } from '../../services/users.service';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'users-login',
  templateUrl: './login.component.html',
  styleUrl: './login.component.scss',
  imports: [
    FormsModule,
    ReactiveFormsModule,
    MatInputModule,
    MatFormFieldModule,
    MatButtonModule,
  ],
})
export class LoginComponent implements OnInit, OnDestroy {
  private formBuilder = inject(FormBuilder);
  private auth = inject(AuthService);
  private localstorageService = inject(LocalstorageService);
  private router = inject(Router);
  private authGuard = inject(AuthGuard);
  private usersService = inject(UsersService);
  private _snackBar = inject(MatSnackBar);

  loginFormGroup!: FormGroup;
  isSubmitted = false;
  isGuestSubmitted = false;
  authError = false;
  authMessage = 'Email or Password are wrong';
  subscriptions = new Subscription();

  ngOnInit(): void {
    if (this.authGuard.canActivate()) {
      this.router.navigateByUrl('/');
    }

    this._initLoginForm();

    const subscription = this.loginFormGroup.valueChanges.subscribe(change => {
      if (change) this.authError = false;
    });
    this.subscriptions.add(subscription);
  }

  private _initLoginForm() {
    this.loginFormGroup = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', Validators.required],
    });
  }

  onSubmit() {
    if (this.loginFormGroup.invalid) return;
    this.isSubmitted = true;

    const subscription = this.auth
      .login(this.loginForm['email'].value, this.loginForm['password'].value)
      .subscribe({
        next: user => {
          this.authError = false;
          const token = user.token;
          let tokenDecode: any;

          this.localstorageService.setToken('jwtToken', token);
          console.log(token);

          // On login, check if there's 'admin' in path and redirect root path or language specific path
          const isAdminPath = window.location.pathname.includes('/admin');
          console.log('Is Admin Path:', isAdminPath);

          // Set user language
          if (token) {
            tokenDecode = JSON.parse(atob(token.split('.')[1]));
            console.log(tokenDecode.userLanguage);
            if (tokenDecode.userLanguage) {
              this.localstorageService.setToken(
                'userLanguage',
                tokenDecode.userLanguage,
              );
            }
            if (isAdminPath) {
              this.router.navigate(['/']);
            } else {
              console.log('navigate to lang build');
              location.replace(
                `${window.location.origin}/${tokenDecode.userLanguage}/`,
              );
            }
            // this.router.navigate([`/${tokenDecode.userLanguage}`]);
            console.log('navigzte here');
          } else {
            this.router.navigate(['/']);
          }
        },
        error: error => {
          this.isSubmitted = false;
          this.authError = true;

          if (error.status !== 400) {
            this.authMessage = 'Error in the server, please try again later';
          }
        },
      });

    this.subscriptions.add(subscription);
  }

  onSubmitGuest() {
    this.usersService.loginAsGuest().subscribe({
      next: data => {
        console.log(data);
        const token = data.token;

        this.localstorageService.setToken('jwtToken', token);
        this.localstorageService.setToken(
          'userLanguage',
          'en-US',
        );
        this.router.navigate(['/']); // Redirect to app
      },
      error: err => {
        this._snackBar.open(
          $localize`:@@Snackbar.loginError:Too many login attempts. Try again in 15 minutes.`,
          '',
          {
            duration: 2000,
            panelClass: ['custom-snackbar-style'],
          },
        );
      },
    });
  }

  get loginForm() {
    return this.loginFormGroup?.controls;
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
