import { Injectable, inject } from '@angular/core';
import { Router } from '@angular/router';
import { LocalstorageService } from './localstorage.service';

@Injectable({
  providedIn: 'root',
})
export class AuthGuard {
  private router = inject(Router);
  private localStorageToken = inject(LocalstorageService);


  canActivate() {
    const token = this.localStorageToken.getToken('jwtToken');

    if (token) {
      const tokenDecode = JSON.parse(atob(token.split('.')[1]));
      if(tokenDecode.userId && !this._tokenExpired(tokenDecode.exp)) {
          // On login, check if there's 'admin' in path and if user is admin
          const isAdminPath = window.location.pathname.includes('/admin');
          if(isAdminPath) {
            return tokenDecode.isAdmin ? true : false;
          }

        return true;
      }
    }
    this.router.navigate(['/login']);
    return false;
  }

  private _tokenExpired(expiration: number): boolean{
    return Math.floor(new Date().getTime() / 1000) >= expiration;
  }
}
