import { HttpClient } from '@angular/common/http';
import { Injectable, signal, inject } from '@angular/core';
import { Observable, map } from 'rxjs';
import { Favorites, User, UserLanguage } from '../models/user';
import { environment } from '@env/environment';
import { Program } from 'libs/exercises/src/lib/models/program';

@Injectable({
  providedIn: 'root',
})
export class UsersService {
  private http = inject(HttpClient);

  apiURLUsers = environment.apiURL + 'users';

  getUsers(): Observable<User[]> {
    return this.http.get<User[]>(this.apiURLUsers);
  }

  getUser(userId: string): Observable<any> {
    return this.http.get<any>(`${this.apiURLUsers}/${userId}`);
  }

  createUser(user: FormData): Observable<User> {
    return this.http.post<User>(this.apiURLUsers, user);
  }

  updateUser(user: FormData, userId: string): Observable<User> {
    return this.http.put<User>(`${this.apiURLUsers}/${userId}`, user);
  }

  deleteUser(userId: string): Observable<User> {
    return this.http.delete<User>(`${this.apiURLUsers}/${userId}`);
  }

  getFavorites(userId: string): Observable<Favorites> {
    return this.http.get<any>(`${this.apiURLUsers}/favorites/${userId}`).pipe(
      map(favoritesData => {
        console.log('Get favorites data service: ', favoritesData);
        this.userName.set(
          favoritesData.firstName + ' ' + favoritesData.lastName,
        );

        this.profileThumbnail.set(favoritesData.profilePictureThumb);

        this.favoritePrograms.set(favoritesData.favorites.favPrograms);

        this.favoriteExercises.set(favoritesData.favorites.favExercises);

        const normalizedFavorites = {
          ...favoritesData.favorites,
          favPrograms: favoritesData.favorites.favPrograms.map(
            (program: { id: any }) => program.id,
          ),
          favExercises: favoritesData.favorites.favExercises.map(
            (exercise: { id: any }) => exercise.id,
          ),
        };

        return normalizedFavorites as Favorites;
      }),
    );
  }

  updateFavorites(userId: string, favorites: Favorites): Observable<Favorites> {
    return this.http
      .patch<any>(`${this.apiURLUsers}/favorites/${userId}`, favorites)
      .pipe(
        map(favoritesData => {
          console.log('Favorites data after update: ', favoritesData);

          this.favoritePrograms.set(favoritesData.favorites.favPrograms);
          this.favoriteExercises.set(favoritesData.favorites.favExercises);

          return favoritesData as Favorites;
        }),
      );
  }

  getUserLanguage(userId: string): Observable<UserLanguage> {
    return this.http.get<UserLanguage>(
      `${this.apiURLUsers}/user-language/${userId}`,
    );
    // .pipe(
    //   map(userLanguage => {
    //     return userLanguage.userLanguage;
    //   }),
    // );
  }

  /*   getProfileThumbnail(userId: string): Observable<string> {
    return this.http.get<string>(
      `${this.apiURLUsers}/profilePicThumb/${userId}`,
    );
  } */

  updateUserLanguage(
    userId: string,
    userLanguageCode: UserLanguage,
  ): Observable<string> {
    return this.http.patch<string>(
      `${this.apiURLUsers}/user-language/${userId}`,
      userLanguageCode,
    );
  }

  loginAsGuest(): Observable<{ username: string; token: string }> {
    return this.http.get<{ username: string; token: string }>(
      `${this.apiURLUsers}/guest/login`,
    );
  }

  userId = signal<string>('');

  isAdmin = signal<boolean>(false);

  userName = signal<string>('');

  profileThumbnail = signal<string>('');

  userFavorites = signal<Favorites>({ favExercises: [], favPrograms: [] });

  favoritePrograms = signal<any[]>([]);
  favoriteExercises = signal<any[]>([]);
}
